* {
  font-family: 'Roboto', sans-serif;
  margin: 0;
}

.calendar-parent {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 99999;
  background-color: rgba(0, 0, 0, 0.5);
  .calendar-center {
    background-color: #fff;
    overflow: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 8px;
    box-shadow: 0 0 10px 3px #636363;
    h2 {
      font-weight: 400;
      font-size: 18px;
      margin-bottom: 30px;
      color: #061211;
      letter-spacing: 0.11px;
      margin: 24px 0 0 24px;
    }
    .checkbox-wrapper {
      display: flex;
      align-items: center;
      // margin-bottom: 20px;
      margin: 24px 0 20px 24px;
      .checkbox {
        width: 16px;
        height: 16px;
        border: 1px #d1dada solid;
        border-radius: 2px;
        margin-right: 10px;
        cursor: pointer;
        position: relative;

        .tick {
          transform: rotate(-50deg);
          position: absolute;
          width: 7px;
          height: 4px;
          top: 3px;
          left: 3px;
          border-bottom: 3px solid #fff;
          border-left: 3px solid #fff;
        }
      }
      p {
        color: #0e131f;
        font-size: 12px;
        letter-spacing: 0.15px;
      }
    }
    .date-input {
      display: flex;
      justify-content: space-between;
      padding: 0 24px 10px 24px;
      .date-selected {
        width: 47%;
        & > p {
          color: #0e131f;
          font-size: 12px;
          letter-spacing: 0.15px;
          margin-bottom: 8px;
        }
        .date-text {
          border: 1px solid #ebebeb;
          padding: 12px 16px;
          border-radius: 8px;
          box-sizing: border-box;
          span {
            color: #0e131f;
            font-size: 14px;
            margin-right: 10px;
            letter-spacing: 0.18px;
            &:nth-child(2) {
              color: #ababab;
              font-weight: bold;
              font-size: 16px;
            }
          }
        }
      }
    }

    .calendar-wrapper {
      background-color: #f8f8f8;
      padding: 24px;
      // margin-top: 18px;
    }
  }

  .CalendarDay__default {
    border: none;
    &:hover {
      background: #a7dcd2;
      border-radius: 50%;
      color: #000;
    }
  }
  small {
    font-size: 14px;
  }
  strong {
    font-weight: 400;
    font-size: 14px;
    color: #4e5f5e;
  }
  tr {
    td {
      &:nth-child(1) {
        color: #ff8585;
      }
      &:nth-child(7) {
        color: #ff8585;
      }
    }
  }
  .DayPicker_weekHeader_ul {
    li {
      &:nth-child(1) {
        color: #ff8585;
      }
      &:nth-child(7) {
        color: #ff8585;
      }
    }
  }
  .DayPicker_transitionContainer {
    //margin-top: 20px;
    border-radius: 8px;
    box-shadow: 0px 2px 7px 2px #a9a9a9;
  }
  .DayPicker__withBorder {
    box-shadow: none;
  }
  .CalendarDay__selected {
    background: #a7dcd2;
    border-radius: 50%;
    color: #000;
  }
  .CalendarDay__selected_span {
    background-color: #ececec;
    color: #000;
  }
  .CalendarDay__hovered_span {
    background-color: #ececec;
    color: #000;
  }
  .CalendarDay__highlighted_calendar {
    background-color: #fcbf49;
  }
}
